.navContainer{
    background-color: black;
    height: 500px;
}

.navContainer{
    display: flex;
    width: 100vw;
   
}
ul{
    display: flex;
   text-decoration: none;
   list-style: none;
   
   
}

a{
    
    color: white!important;
    margin-left: 80px;
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
    
}
.navLogo{
    
    color: white;
}
.navLogo h4{
    margin-top: 1em;
    margin-left: 10px;
    margin-bottom: 5px;
    font-size: 100px;
}
.navLogo p{
    margin-left: 8%;
    font-size: 30px;
    color: palevioletred;
}

.navMenu{
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 50px;
    text-align: right;
    top: 1%;
}